import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Calendar2 from "react-calendar";
import Grid from "@material-ui/core/Grid";
import { isSameDay } from "date-fns";
import { generateISODate } from "../../../assets/helper/constants";

const useStyles = makeStyles((theme) => ({
  root: {
    padding: theme.spacing(5),
  },
}));

const Calendar = (props) => {
  const { formData, slots_bulk_list } = props;
  const classes = useStyles();
  const [value, onChange] = useState(new Date());

  function tileDisabled({ date, view }) {
    // Disable tiles in month view only
    if (view === "month") {
      if (slots_bulk_list && slots_bulk_list.length > 0) {
        // Check if a date React-Calendar wants to check is on the list of disabled dates
        let dateValidate = slots_bulk_list.find((dDate) => dDate.date === generateISODate(date));
        if (dateValidate && dateValidate.is_available) {
          return false;
        } else {
          return true;
        }
      } else {
        return true;
      }
    }
  }

  return (
    <Grid
      container
      className={classes.root}
      align="center"
      justifyContent="center"
      alignItems="center"
    >
      <Calendar2
        onChange={onChange}
        value={value}
        tileDisabled={tileDisabled}
        activeStartDate={
          formData && formData.from_date ? formData.from_date : new Date()
        }
        minDate={formData && formData.from_date ? formData.from_date : false}
        maxDate={formData && formData.to_date ? formData.to_date : false}
        calendarType="US"
        showNavigation={true}
        showNeighboringMonth={true}
      />
    </Grid>
  );
};

export default Calendar;
